import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { UserService } from 'src/app/services/user.service';

@Component({
  selector: 'app-show-products',
  templateUrl: './show-products.component.html',
  styleUrls: ['./show-products.component.css']
})
export class ShowproductsComponent implements OnInit {

  constructor(private userService: UserService) { }


  quantity: number | undefined;

  medicines = [{
    medid: '',
    medname: '',
    price: '',
    description: '',
    enabled: true,
    category: '',
    quantity: '',
    dosage: ''
  }];

  medicines_temp_keyword = [{
    medid: '',
    medname: '',
    price: '',
    description: '',
    enabled: true,
    category: '',
    quantity: '',
    dosage: ''
  }];

  medicines_temp_filter = [{
    medid: '',
    medname: '',
    price: '',
    description: '',
    enabled: true,
    category: '',
    quantity: '',
    dosage: ''
  }];



  categories = [
    "DEPRESSION",
    "BLOOD PRESSURE",
    "DIABETES",
    "ARTERY HEALTH"
  ];

  keywordEntered: any;
  filterSelected: any;

  isSearch: boolean = false;
  isFilter: boolean = false;

  doFilterSelected() {
    console.log(this.filterSelected)
  }

  fullCategories = [
    { id: 0, cat: "ALL CATEGORY TYPES" },
    { id: 1, cat: "DIABETES", A: "Glyburide", B: "Glipizide", C: "Riomet", D: "Segluromet", E: "Segluromet" },
    { id: 2, cat: "BLOOD PRESSURE", A: "Lisinopril", B: "Furosemide", C: "Propranolol", D: "Clonidine", E: "Furosemide" },
    { id: 3, cat: "DEPRESSION", A: "Bupropion", B: "Nortriptyline", C: "Doxepin", D: "Citalopram", E: "Duloxetine" },
    { id: 4, cat: "ARTERY HEALTH", A: "Metformin ", B: "Losartan", C: "Hydrochlorothiazide", D: "Atorvastatin", E: "Amlodipine" },
  ];



  ngOnInit(): void {
    this.userService.getMedicines().subscribe(
      (data: any) => {
        this.medicines = data;
        console.log(this.medicines);

      },
      (error) => {
        console.log("error");
      }
    );

  }



  //XXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXX
  public search(f: NgForm) {

    this.medicines_temp_keyword = [];
    this.medicines_temp_filter = [];

    this.isSearch = false;
    this.isFilter = false;

    //zxzxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx
    //>  DO SEARCH (KEYWORD) PULL PROCEDURE
    if (this.keywordEntered != null && this.keywordEntered != '' && this.keywordEntered != 'undefined') {
      this.isSearch = true;
      console.log("THIS KEYWORD :" + f.value.keyword);
    }


    //zxzxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx
    //>  DO FILTER (CATEGORY) PULL PROCEDURE
    if (this.filterSelected != null && this.filterSelected != '' && this.filterSelected != 'undefined' && this.filterSelected != 'ALL CATEGORY TYPES') {
      console.log("THIS FILTER :" + f.value.filterword);
      
      this.isFilter = true;
    }



    this.medicines = [];
   










    //zxzxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx
    //>  NEITHER  FILTER and SEARCH results are NULL
    if (!this.isSearch && !this.isFilter) {

      console.log("<<EMPTY>>>  BOTH FILTER and SEARCH results are NULL ");
      this.userService.getMedicines().subscribe(
        (data: any) => {



          this.medicines = data;
          console.log(this.medicines);
  
        },
        (error) => {
          console.log("error");
        }
      );
  
      this.medicines == null;


    } else if (this.isSearch && !this.isFilter) {
      
      //zxzxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx
      //>  JUST - SEARCH FINAL MED-LIST REQUIRED
      console.log("PERFORMING FINAL (BRAND NAME - KEYWORD ONLIY) MEDICATION LIST BUILD");
        this.userService.searchByName(f.value.keyword).subscribe(
          (data: any) => {
            this.medicines = data;
            this.medicines_temp_keyword = data;
            console.log(this.medicines);
          }
        );
      
      
    
    } else if (!this.isSearch && this.isFilter) {
      //zxzxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx
      //>  JUST - SEARCH FINAL MED-LIST REQUIRED

      console.log("PERFORMING FINAL (BRAND NAME - FILTER ONLY for ("+this.filterSelected+")) MEDICATION LIST BUILD");

      if(this.filterSelected == 'ALL CATEGORY TYPES'){
        this.userService.getMedicines().subscribe(
          (data: any) => {
            this.medicines = data;
            this.medicines_temp_filter = data;
            console.log(this.medicines);
          },
          (error) => {
            console.log("error");
          }
        );

      }else{

          this.userService.searchByCategory(f.value.filterword).subscribe(
            (data: any) => {
              this.medicines = data;
              console.log(this.medicines);
            }
          );
      }



    } else if (this.isSearch && this.isFilter) {
      //zxzxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx
      //>  DUAL SEARCH/FILTERING PROCEDURE  (Compare BOTH Lists)

  
        console.log("PERFORMING DUAL (SEARCH and FILTER) LIST COMPARE");
        if (this.medicines_temp_filter.length > this.medicines_temp_keyword.length) {

          this.medicines_temp_filter.forEach(element => {
            var doesExist = this.medicines_temp_keyword.indexOf(element);
            // (-1) if NOT DOES NOT EXIST
            if (doesExist != -1) {
              this.medicines.push(element);
            }
          });


        } else {

          this.medicines_temp_keyword.forEach(element => {

            var doesExist = this.medicines_temp_filter.indexOf(element);
            // (-1) if NOT DOES NOT EXIST
            if (doesExist != -1) {
              this.medicines.push(element);
            }
          });
        }

      
      console.log("TOTAL MEDICATIONS FOUND LENGTH = " + this.medicines.length);
    } 
  }
}
