import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from 'src/app/services/admin.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-remove-medicine',
  templateUrl: './remove-medicine.component.html',
  styleUrls: ['./remove-medicine.component.css']
})
export class RemovemedicineComponent implements OnInit {

  medicine={
    medid:'',
    medname:'',
    price:'',
    enabled:true,
    category:'',
    quantity:'',
    dosage:'',
    description:''
  };

  id:number=-1;
  sub:any;

  constructor(private _Activatedroute:ActivatedRoute,private route:Router,private adminService:AdminService) { }

  ngOnInit(): void {
    this.sub=this._Activatedroute.params.subscribe(params => { 
      this.id = params['medid']; 
      this.adminService.getMedicineByID(this.id).subscribe(
        (data:any)=>{
          this.medicine=data;
          console.log(this.medicine);
        },
        (error)=>{
          console.log("error");
        }
      );  
    });
  }

  public removeMedicine(){
      this.adminService.removeMedicine(this.id).subscribe((data:any)=>{
      console.log('success');
      Swal.fire('success',`${this.medicine.medname} removed from database successfully`,'success');
      this.route.navigate(['admin/view-medicines']);
    },(error)=>{
      console.log("error");
      Swal.fire('Error',"Server Error",'error');
      this.route.navigate(['admin/view-medicines']);
    });

  }

  public goback(){
    this.route.navigate(['admin/view-medicines']);
  }
   
  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}
