import { Component, OnInit } from '@angular/core';
import { LoginService } from 'src/app/services/login.service';
import { UserService } from 'src/app/services/user.service';

@Component({
  selector: 'app-user-purchases',
  templateUrl: './user-purchases.component.html',
  styleUrls: ['./user-purchases.component.css']
})
export class PurchasesumComponent implements OnInit {

  id:number=-1;
  total:number=0;
  purchaseitems:any=[];
  constructor(private loginService:LoginService,private userService:UserService) { }

  ngOnInit(): void {
    this.id=this.loginService.getUser().id;
    this.userService.purchaseItems(this.id).subscribe(
      (data:any)=>{
        this.purchaseitems=data;
        console.log(this.purchaseitems);
        for (var item of this.purchaseitems) {
          this.total = item.quantity * item.price;
        }
      },
      (error)=>{
        console.log(error);
      }
    );
  }
}