import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { LoginService } from 'src/app/services/login.service';
import { UserService } from 'src/app/services/user.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-user-addtocart',
  templateUrl: './user-addtocart.component.html',
  styleUrls: ['./user-addtocart.component.css']
})
export class UseraddtocartComponent implements OnInit {
  
  medicine={
    medid:'',
    medname:'',
    price:'',
    enabled:true,
    category:'',
    quantity:'',
    dosage:'',
    description:''
  };
  
  addToCartJSON={
    userid:'',
    medid:'',
    quantity:''
  }

  id:number = -1;
  sub:any;
  
  constructor(private _Activatedroute:ActivatedRoute,private userService:UserService,private loginService:LoginService, private snack:MatSnackBar) { }

  ngOnInit(): void {

    this.sub=this._Activatedroute.paramMap.subscribe(params => {

      console.log("[UseraddtocartComponent] THIS MED ID IS :"+params.get('medid'));

      if(params.get('medid') == undefined){
        this.snack.open("THIS MEDICINE-ID IS 'UNDEFINED'!",'',
        {duration:3000,verticalPosition:'top',horizontalPosition:'end'});

      }else{

        this.id = parseFloat((params.get('medid')|| '{}'));

        this.userService.getMedicineByID(this.id).subscribe(
          (data:any)=>{
            this.medicine=data;
            console.log(this.medicine);
          },
          (error)=>{
            console.log("error");
          }
        );  
      }

     
    });

    this.sub=this._Activatedroute.params.subscribe(params => { 

      this.id = params['medid']; 

      if(this.id == undefined){
        this.snack.open("THIS MEDICINE-ID IS 'UNDEFINED'!",'',
        {duration:3000,verticalPosition:'top',horizontalPosition:'end'});

      }else{

        this.userService.getMedicineByID(this.id).subscribe(
          (data:any)=>{
            this.medicine=data;
            console.log(this.medicine);
          },
          (error)=>{
            console.log("error");
          }
        );  
      }

     
    });
  }
  
  addToCart(f:NgForm){
     this.addToCartJSON.quantity=f.value.quantity;
     this.addToCartJSON.medid=this.id.toString();
     this.addToCartJSON.userid=this.loginService.getUser().id;
     
     this.userService.addToCart(this.addToCartJSON).subscribe(
       (data:any)=>{
         console.log('Success');
         Swal.fire('success','Medicine Saved to DB','success');
       },
       (error)=>{
         console.log('Error');
         Swal.fire('error','Server error','error');   
       }
     );
  }
}
