import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from 'src/app/services/admin.service';
import Swal from 'sweetalert2';
import { MatSnackBar } from '@angular/material/snack-bar';
import { UserService } from 'src/app/services/user.service';

@Component({
  selector: 'app-update-medicine',
  templateUrl: './update-medicine.component.html',
  styleUrls: ['./update-medicine.component.css']
})
export class UpdatemedicineComponent implements OnInit {
   
  medicine={
    medid:'',
    medname:'',
    price:'',
    enabled:true,
    category:'',
    quantity:'',
    dosage:'',
    description:''
  };

  id:number=-1;
  sub:any;

  constructor(private _Activatedroute:ActivatedRoute,private route:Router,private adminService:AdminService, private userService:UserService,private snack:MatSnackBar) { }



  ngOnInit(): void {

    this.sub=this._Activatedroute.paramMap.subscribe(params => {

      console.log("[AdminaddtocartComponent] THIS MED ID IS :"+params.get('medid'));

      if(params.get('medid') == undefined){
        this.snack.open("THIS MEDICINE-ID IS 'UNDEFINED'!",'',
        {duration:3000,verticalPosition:'top',horizontalPosition:'end'});

      }else{

        this.id = parseFloat((params.get('medid')|| '{}'));

        this.adminService.getMedicineByID(this.id).subscribe(
          (data:any)=>{
            this.medicine=data;
            console.log(this.medicine);
          },
          (error)=>{
            console.log("error");
          }
        );  
      }

     
    });

    this.sub=this._Activatedroute.params.subscribe(params => { 

      this.id = params['medid']; 

      if(this.id == undefined){
        this.snack.open("THIS MEDICINE-ID IS 'UNDEFINED'!",'',
        {duration:3000,verticalPosition:'top',horizontalPosition:'end'});

      }else{

        this.adminService.getMedicineByID(this.id).subscribe(
          (data:any)=>{
            this.medicine=data;
            console.log(this.medicine);
          },
          (error)=>{
            console.log("error");
          }
        );  
      }

     
    });
  }

  
   public updMedicine(){
      this.adminService.updateMedicine(this.id,this.medicine).subscribe(
        (data:any)=>{
          console.log('Success');
          Swal.fire('success',`${this.medicine.medname} updated successfully`,'success');
          this.route.navigate(['admin/view-medicines']);
        },
        (error)=>{
          console.log('error');
          Swal.fire('Error',"Server Error",'error');
          this.route.navigate(['admin/view-medicines']);
        }
      );
   }


  }
